// Importing modules
import React, { useContext, useEffect, useState } from "react";
import {
  Link,
  json,
  useNavigate,
  useActionData,
  
} from "react-router-dom";
import AlertCard from "../../UI/MessageBox/AlertCard";
import Button from "../../UI/Button/Button";
import AuthContext from "../../../store/auth-context";



function LoginForm() {

  const [identity, setIdentity] = useState("");
  const [password, setPassword] = useState("");
  const [identityValidation, setIdentityValidation] = useState(false);
  const [passwordValidation, setPasswordValidation] = useState(false);
  const [identityTouched, setIdentityTouched] = useState(false);
  const [passwordTouched, setPasswordTouched] = useState(false);
  const [isFormValid, setIsFormValid] = useState(false);
  //const [error, setError] = useState(null);
  //const submit = useSubmit();
  


  useEffect(() => {
    if (identity.trim().length > 0) {
      setIdentityValidation(true);
    } else {
      setIdentityValidation(false);
    }
    //console.log("identity valid: " + identityValidation);
    if (password.trim().length > 0) {
      setPasswordValidation(true);
    } else {
      setPasswordValidation(false);
    }
    //console.log("pwd valid: " + passwordValidation);
    // validate form logic
    // if (identity.trim().length === 7 || identity.includes('@')) {
    //    setIdentityValidation(true);
    //  } 
    if (identityValidation && passwordValidation) {
      setIsFormValid(true);
    } else {
      setIsFormValid(false);
    }

    // add timeout here to reduce time of request every changing
}, [identity, password, identityValidation, passwordValidation]);
  
  //console.log("form valid: " + isFormValid);

  const identityChangeHandler = (event) => {
    setIdentity(event.target.value);
    setIdentityTouched(true);
  };
  const passwordChangeHandler = (event) => {
    setPassword(event.target.value);
    setPasswordTouched(true);
  };

  /* const loginHandler = (event) => {
    event.preventDefault();
    authctx.onLogin(identity, password);
  }*/
  const authctx = useContext(AuthContext);
  const navigate = useNavigate();
  
  const loginHandler = async (event) => {
    event.preventDefault();
    // validate input data
    if (identity.trim().length === 0) {
      setIdentityValidation(false);
      return;
    }
    //console.log("identity valid: " + identityValidation);
    if (password.trim().length === 0) {
      setPasswordValidation(false);
      return;
    }
    //console.log("pwd valid: " + passwordValidation);
    // validate form logic
    // if (identity.trim().length === 7 || identity.includes('@')) {
    //    setIdentityValidation(true);
    //  } 
    if (!identityValidation || !passwordValidation) {
      setIsFormValid(false);
      return;
    }

    const authData = {
      identity: identity,
      password: password,
    };
    // submit hook
    //submit({authData: authData}, {method: "post", action: "/login"});

    // send request to backend API
    
    const login_url = "http://127.0.0.1:5000/login";
    try {
      // send HTTP request
      const response = await fetch(login_url, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(authData),
      });

      // check errors
      if (!response.ok) {
        return response;
      }
      //console.log("Response:");
      //console.log(response);

      // successfully login
      const resData = await response.json();
      //console.log("Response Data:");
      //console.log(resData);
      if (resData.status !== 200) {
        return resData;
      }
      const token = resData.token;
      //console.log(token);
      if (token) {

        localStorage.setItem("token", token);
        authctx.onLogin();

        // forced reset password
        const forcedResetPwd = resData.forceReset;
        /* console.log("return forced reset value:");
        console.log(forcedResetPwd); */
        if (!forcedResetPwd) {
          //authctx.onForcedReset();
          return navigate("/forced_reset_password");
        } else {
          localStorage.setItem("forced_reset", "1");
          authctx.onForcedReset();

          const expiration = new Date();
          expiration.setMinutes(expiration.getMinutes() + 30); // + 30 minutes in future
          localStorage.setItem("expiration", expiration.toISOString());
          // redirect
          return navigate("/status");
        }
      }

    } catch (error) {
      console.log("Catch errors:");
      console.log(error);
      throw json(error);
    }
    
  };



  // get data of response http request
  //const navigation = useNavigation();
  //const isSubmitting = navigation.state === "submitting";
  const data = useActionData();
  

  return (
    <form id="LoginForm" method="POST" onSubmit={loginHandler}>
      {data && data.message && (
        <AlertCard variant="danger">{data.message}</AlertCard>
      )}
      <div className="form-field my-3">
        <label htmlFor="identity">Email Address/UserID *</label>
        <input
          type="text"
          id="identity"
          name="identity"
          value={identity}
          onChange={identityChangeHandler}
          className={`${
            identityTouched && !identityValidation
              ? "form-control invalid_input"
              : "form-control"
          }`}
        />
        {identityTouched && !identityValidation && (
          <p className="error-text">The identity must be empty!</p>
        )}
      </div>
      <div className="form-field my-3">
        <label htmlFor="password">Password *</label>
        <input
          type="password"
          id="password"
          name="password"
          value={password}
          onChange={passwordChangeHandler}
          className={`${
            passwordTouched && !passwordValidation
              ? "form-control invalid_input"
              : "form-control"
          }`}
        />
        {passwordTouched && !passwordValidation && (
          <p className="error-text">The password must not be empty!</p>
        )}
        <div className="mt-2 d-flex justify-content-end">
          <Link
            to="/forgot_password"
            className="dashboard_link text_color_grey fs-7"
            title="Forgot Password"
          >
            Forgot your password?
          </Link>
        </div>
      </div>

      <div className="text-center mt-4">
        <Button type="submit" className="login_btn" disabled={!isFormValid}>
          Login
        </Button>
      </div>
    </form>
  );
}

export default LoginForm;

