import React, { useEffect, useContext, Suspense } from "react";

import { Outlet, useLoaderData, useSubmit, defer, Await } from "react-router-dom";

import TopBar from "../PageLayout/TopBar/TopBar";
import MainNavigation from "../PageLayout/MainNavigation/MainNavigation";
import Footer from "../PageLayout/Footer/Footer";
import AuthContext from "../../store/auth-context";
import ThemeContext from "../../store/theme-context";
import ForcedResetPassword from "./ForcedReset/ForcedResetPassword";
import { getTokenDuration } from "../utils/auth";

const RootLayout = () => {
  const themCtx = useContext(ThemeContext);
  const authctx = useContext(AuthContext);
  console.log(authctx); // => why the forced_reset here is always False ???
  const loaderData = useLoaderData();
  /* console.log("loader data");
  console.log(loaderData); */
  const { token, forced_reset } = loaderData;
  //console.log("forced_reset from loader data");
  //console.log(forced_reset);
  const submit = useSubmit();
   useEffect(() => {
     if (!token) {
       return;
     }
    if (token === "EXPIRED") {
      submit(null, { action: "/logout", method: "post" });
      return;
    }
     const tokenDuration = getTokenDuration();
     setTimeout(() => {
       submit(null, { action: "/logout", method: "post" });
     }, tokenDuration);
   }, [token, submit]);

  
  

  return (
    <React.Fragment>
      <div className="fluid-container">
        <div className="row mx-0">
          {token && (
            <div className="col-12 col-lg-2 col-xl-2 px-0 left_sidebar_wrap">
              <MainNavigation />
            </div>
          )}
          <div
            className={`col-12 col-lg-10 col-xl-10 px-0 right_content ${
              themCtx.darkmode ? "darkgrey_bg" : "lightgrey_bg"
            }`}
          >
            <div className="fluid-container ">
              <div className="row mx-0">
                <TopBar />
              </div>
              {token && (
                <div className={`main_content_container `}>
                  {!forced_reset && <ForcedResetPassword />}
                  {forced_reset && <Outlet />}
                </div>
              )}
              <Footer />
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default RootLayout;
