import React, { useEffect, useState } from "react";
import { getAuthToken, getForcedResetStatus } from "../components/utils/auth";


const AuthContext = React.createContext({
    isLoggedIn: false,
    isForcedReset: false,
    onLogin: () => { },
    onLogout: () => { },
    onForcedReset: () => {}
});

export const AuthContextProvider = (props) => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [isForcedReset, setIsForcedReset] = useState(false);

  useEffect(() => {
    //console.log("useEffect is running");
    const token = getAuthToken();

    if (token) {
      setIsLoggedIn(true);
    }
  }, []);
  
    const logoutHandler = () => {
        localStorage.removeItem("token");
      localStorage.removeItem("forced_reset");
      localStorage.removeItem("expiration");
        setIsLoggedIn(false);
        setIsForcedReset(false);
    }
    const loginHandler = () => {
      console.log("loginHandler in auth context is called");
      //localStorage.setItem("token", token);
      //setIsLoggedIn(true);
      const token = getAuthToken();
      if (token) {
       setIsLoggedIn(true);
      }      
    };
    
    
  const forcedResetHandler = () => {

        const forcedReset = getForcedResetStatus();
        if (forcedReset === "1") {
          setIsForcedReset(true);
        }
    }
    

  return (
    <AuthContext.Provider
      value={{
        isLoggedIn: isLoggedIn,
        isForcedReset: isForcedReset,
        onLogin: loginHandler,
        onLogout: logoutHandler,
        onForcedReset: forcedResetHandler,
      }}
    >
      {props.children}
    </AuthContext.Provider>
  );
}


export default AuthContext;