import React, { Suspense} from "react";
import { useRouteLoaderData, json, defer, Await, redirect} from "react-router-dom";
import TokenDetailItem from "./TokenDetailItem";
import { getAuthToken } from "../../utils/auth";


const TokenDetail = () => {
    const token = useRouteLoaderData("token-detail");
    console.log("token detail");
    console.log(token);
    return (
      <React.Fragment>
        <p>Token detail page</p>
        <Suspense fallback={<p style={{ textAlign: "center" }}>Loading...</p>}>
          <Await resolve={token}>
            {(loadedToken) => <TokenDetailItem token={loadedToken} />}
          </Await>
        </Suspense>
      </React.Fragment>
    );
}

export default TokenDetail;

async function loaderToken(id) {
  const response = await fetch("http://127.0.0.1:5000/tokens/" + id);
  if (!response.ok) {
    throw json(
      { message: "Could not fetch details for selected token." },
      { status: 500 }
    );
  } else {
    const resData = await response.json();
    return resData.token;
  }
}

export async function loader({ request, params }) {
  const id = params.tokenId;
  return defer({
    token: await loaderToken(id)
  });
}

export async function action({ request, params }) {
  const tokenId = params.tokenId;

  const auth_token = getAuthToken();
  const revoke_url = "http://127.0.0.1:5000/tokens/" + tokenId;
  const response = await fetch(revoke_url, {
    method: "DELETE",
    headers: {
      Authorization: "Bearer " + auth_token
    },
  });

  if (!response.ok) {
    throw json({ message: "Could not revoke token." }, { status: 500 });
  } 
  return redirect("/tokens");
}