import React, { useState, useEffect, useContext, Suspense} from "react";
import { Navigate, defer, Await, json, redirect, useLoaderData } from "react-router-dom";
import JobsList from "./JobsList";
import JobsSorting from "./JobsSorting";
import AuthContext from "../../../store/auth-context";

import './Jobs.scss';
import JobsFilter from "./JobsFilter";
import { getAuthToken } from "../../utils/auth";

function Jobs() {
  
  const jobs = useLoaderData();
  const [sortingProperty, setSortingProperty] = useState("id");
  //const [sortedJobs, setSortedJobs] = useState(tempJobs);
  /* useEffect(() => {
    // sorting here by call function sort()
    sortedJobs = tempJobs.sort(
      (job_1, job_2) => job_1.sortingProperty - job_2.sortingProperty
    );
  }, [sortedJobs]); */
  const sortingJobsHandler = (sortedProperty) => {
    setSortingProperty(sortedProperty);
  };

  
  return (
    <React.Fragment>
      <div className={`listJob_container`}>
        <div className="container_header_wrap">
          <h4 className="page_header">Jobs Running</h4>
        </div>
        <div className="jobs_actions">
          <JobsSorting onSorting={sortingJobsHandler} />

          <JobsFilter />
        </div>
        <Suspense fallback={<p style={{ textAlign: "center" }}>Loading...</p>}>
          <Await resolve={jobs}>
            {(loadedJobs) => <JobsList items={loadedJobs} />}
          </Await>
        </Suspense>
      </div>
    </React.Fragment>
  );
}

export default Jobs;

async function loaderJobsList() {
  const auth_token = getAuthToken();
  const jobs_url = "http://127.0.0.1:5000/jobs";
  const response = await fetch(jobs_url, {
    method: "GET",
    headers: {
      "Authorization": "Bearer " + auth_token,
      "Content-Type": "application/json"
    }
  });

  if (!response.ok) {
    return response;
  }
  else {
    const resData = await response.json();
    return resData;
  }

}
export async function loader() {
  return defer({
    jobs: await loaderJobsList()
  });
}