import { redirect } from "react-router-dom";

export function getTokenDuration() {
    const savedExpirarionDate = localStorage.getItem("expiration");
    const expirationDate = new Date(savedExpirarionDate);
    const now = new Date();
    const duration = expirationDate.getTime() - now.getTime();
    return duration;
}
/*
 * Token
 */
export function getAuthToken() {
    const token = localStorage.getItem('token');
    /* if (!token) {
        return null;
    } */
    /* const tokenDuration = getTokenDuration();
    if (tokenDuration < 0) {
        return 'EXPIRED';
    } */
    return token;
}

export function getForcedResetStatus() {
    const forcedReset = localStorage.getItem("forced_reset");
    /* if (!forcedReset) {
        return null;
    } */
    return forcedReset;
}

export function authLoader() {
    const authLoader = {
        token: getAuthToken(),
        forced_reset: getForcedResetStatus()
    };
    //return getAuthToken();
    return authLoader;
}



export function checkAuthLoader() {
    const token = getAuthToken();
    if (!token) {
        return redirect("/login");
    }
    const forced_reset = getForcedResetStatus();
    if (!forced_reset) {
        return redirect("/forced_reset_password");
    }

    return null;
    
}





