import React, { useState, useContext } from "react";
import { json } from "react-router-dom";
import Card from "../../UI/Card/ContentCard";
import CreateTokenForm from "./CreateTokenForm";
import ThemeContext from "../../../store/theme-context";
import { getAuthToken } from "../../utils/auth";


const NewToken = () => {
    const [toggleFormShow, setToggleFormShow] = useState(false);
  const [toggleButtonShow, setToggleButtonShow] = useState(true);
  
    const toggle_create_token_form = (event) => {
      setToggleFormShow(true);
      setToggleButtonShow(false);
    };
    const hideFormHandler = (isShowed) => {
      setToggleFormShow(isShowed);
      setToggleButtonShow(true);
    };

	const ctx = useContext(ThemeContext);

	return (
    <Card
      className={`${ctx.darkmode ? "dark_bg" : "white_bg"} tokens_container`}
    >
      <div className="createToken_container">
        <div className="text-center">
          {toggleButtonShow && (
            <button
              className="dashboard_btn create_token"
              onClick={toggle_create_token_form}
            >
              Create New Token
            </button>
          )}
        </div>
        {toggleFormShow && <CreateTokenForm onShowing={hideFormHandler} />}
      </div>
    </Card>
  );

}

export default NewToken;

export async function action({request, params}) {
  const tokenData = request.tokenData;
  const token = getAuthToken();

  const tokens_url = "http://127.0.0.1:5000/tokens?action=create";
  const response = await fetch(tokens_url, {
    method: "POST",
    headers: {
      Authorization: "Bearer " + token,
      "Content-Type": "application/json",
    },
    body: JSON.stringify(tokenData),
  });
  if (!response.ok) {
    throw json(
      { message: "Failed to create new token!" }, { status: 500 }
    );
  }
  else {
    const resData = await response.json();
    return resData;
  }

}